
import { reactive, onMounted } from 'vue'
import useCart  from '@/store/store.ts';

export default {
  setup() {
    const items = reactive<string[]>([])
    const { addToCart } = useCart()

    onMounted(() => {
      items.push('apple')
      items.push('banana')
      items.push('orange')
    })

    const getItem = (item:string) => {
      console.log(item);
      addToCart(item);
    }

    return {
      items,
      getItem
    }
  }
}
